.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: rgb(6, 62, 95);
}

input[type="radio"] {
  display: none;
}
.star {
  cursor: pointer;
  transition: color 200ms;
}

p {
  font-family: "Open Sans", sans-serif;
  font-size: 40px;
  font-weight: bold;
}
